.schedule-page {
	&__inner {
		// +ms
 }		// 	padding: 0
	&__head {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		padding-bottom: 76px;
		@include sm {
			padding-bottom: 38px; } }
	&__title {
		max-width: 33%;
		margin-right: auto;
		@include md {
			max-width: none;
			width: 100%; } }
	&__item {
		+ .schedule-page__item {
			margin-top: 80px; } }
	&__tabs {
		@include md {
			padding-top: 18px; } }
	&__list {
		padding-bottom: 20px; } }

.schedule-group {
	@extend %cardShadow;
	@include md {
		background-color: transparent;
		box-shadow: none; }
	&__items {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
		&.slick-slider {
			display: block; } }
	&__title {
		padding: 24px 20px;
		@include md {
			display: none; } }
	&__item {
		width: 100%;
		@include md {
			padding: 0 3px;
			width: 50%;
			.schedule {
				height: 100%; }
			.slick-slide & {
				width: 100%; } } }
	.slick-list {
		@include md {
			overflow: visible; } }
	.slick-track {
		display: flex; }
	.slick-slide > div {
		height: 100%; } }

.schedule {
	display: flex;
	border-top: 2px solid $grayBorder;
	@include lg {
		font-size: 13px; }
	@include md {
		flex-direction: column;
		font-size: 15px;
		border-top: none;
		box-shadow: $shadow;
		border-radius: 10px;
		background-color: $white; }
	@include sm {
		padding-bottom: 8px; }
	& > * {
		padding: 30px 20px;
		@include xl {
			padding: 30px 15px; }
		@include lg {
			padding: 20px 10px; }
		@include md {
			max-width: 100%;
			flex: 0 0 auto; }
		@include sm {
			padding: 20px 12px 22px; }
		&:not(:last-child) {
			border-right: 2px solid $grayBorder;
			@include md {
				border-right: none;
				border-bottom: 2px solid $grayBorder; } }
		&.schedule__block-title {
			padding: 30px 12px; } }
	&__block-title {
		display: none;
		@include md {
			display: block;
			color: #000000;
			font-size: 15px;
			font-weight: 700;
			line-height: 1.667;
			letter-spacing: 0; } }
	&__title {
		margin-bottom: 17px;
		color: $grayLight;
		letter-spacing: 0.033em;
		@include sm {
			margin-bottom: 2px; } }
	&__content {
		font-weight: 700;
		letter-spacing: 0;
		line-height: 1.667;
		&_price {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			@include sm {
				flex-direction: column;
				.btn {
					width: 100%;
					margin-top: 31px; } } } }
	&__date {
		@include flex-size(10.9%);
		span {
			display: block;
			@include sm {
				display: inline; } } }
	&__city {
			@include flex-size(14%); }
	&__direction {
		@include flex-size(11%); }
	&__instructors {
		@include flex-size(16.3%);
		@include xl {
			@include flex-size(20%); }
		.instructor-preview {
			&:not(:last-child) {
				margin-bottom: 15px; }
			@include sm {
				&:first-child {
					margin-top: 8px; } } } }
	&__adress {
		@include flex-size(21%); }
	&__price {
		flex-grow: 1;
		@include md {
			margin-top: auto; } } }

.instructor-preview {
	display: block;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	&__image {
		@include flex-size(40px);
		@include circle(40);
		@include cover;
		margin-right: 10px; }
	&__name {
		@include flex-size(calc(100% - 50px));
		word-wrap: break-word;
		hyphens: auto; } }
