.news-one {
	&__main {
		margin-top: 50px;
		display: flex;
		@include sm {
			margin: 29px -16px 0;
			flex-wrap: wrap;
			justify-content: center; } }
	&__left {
		max-width: 317px;
		width: 100%;
		@include sm {
			max-width: none; } }
	&__right {
		max-width: 700px;
		padding-left: 50px;
		@include md {
			padding-left: 30px; }
		@include sm {
			padding: 0 16px;
			margin-top: 31px; } }
	.date {
		@include sm {
			margin-bottom: 11px; } }
	.title {
		@include sm {
			font-size: 18px; } } }
