.single-course {
	&__main {
		margin-top: 50px;
		display: flex;
		@include sm {
			margin-top: 40px;
			flex-wrap: wrap;
			justify-content: center; } }
	&__left {
		max-width: 423px;
		.course {
			height: auto; } }
	&__right {
		max-width: 700px;
		padding-left: 50px;
		@include md {
			padding-left: 30px; }
		@include sm {
			padding-left: 0;
			margin-top: 40px; } }
	&__buy {
		margin-top: 20px; }
	&__recommend {
		margin-top: 114px;
		@include md {
			margin-top: 77px; } } }

.list {
	&__item {} }

.single-recommend {
	&__row {
		margin-top: 68px;
		@include md {
			margin: 20px -8px -8px; } }
	&__col {
		@include md {
			padding: 8px; } } }
