// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
@include generate_grid($columns: 12, $sizeXs: true, $sizeSm: true, $sizeMd: true, $sizeLg: true, $padding: 15px);
.col-sm-mt {
	&:not(:first-child) {
		@include sm {
			margin-top: 16px; } } }

.out {
	padding-top: 245px;
	overflow: hidden;
	@include md {
		padding-top: 100px; }
	&_sm {
		padding-top: 178px;
		@include md {
			padding-top: 90px; } }
	&_main {
		padding-top: 0;
		@include md {
			padding-top: 60px; } } }

.inner {
	max-width: 1420px;
	margin: 0 auto;
	padding: 0 30px;
	@include md {
		padding: 0 16px; }
	&_md {
		max-width: 1176px; }
	&_wideMd {
		@include md {
			padding: 0; } }
	&_wideSm {
		@include sm {
			padding: 0; } } }

.section {
	&:not(:first-child) {
		padding-top: 150px;
		@include md {
			padding-top: 80px; } } }

.section-md {
	&:not(:first-child) {
		padding-top: 125px;
		@include md {
			padding-top: 80px; } } }

.section-sm {
	&:not(:first-child) {
		padding-top: 70px;
		@include sm {
			padding-top: 40px; } } }

.section,
.section-md,
.section-sm {
	&__title {
		&:not(:last-child) {
			margin-bottom: 70px;
			@include sm {
				margin-bottom: 37px; } } } }
.head {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-between;
	&:not(:last-child) {
		margin-bottom: 70px;
		@include md {
			margin-bottom: 36px; } }
	&__title {
		padding-right: 15px;
		@include sm {
			padding-right: 0; } } }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	clip: rect(0 0 0 0);
	overflow: hidden; }
