.about {
	padding: 144px 0 154px;
	position: relative;
	min-height: 650px;
	// overflow: hidden
	@include sm {
		padding: 60px 0 0; }
	&:before {
		content: '';
		position: absolute;
		top: 50px;
		right: calc(50% + 105px);
		background-color: $blue;
		width: 100%;
		height: 84.6%;
		border: 2px solid $white;
		border-left: none;
		box-shadow: 0 0 0px 50px $blue;
		z-index: -1;
		@include sm {
			display: none; } }
	&__inner {
		max-width: 1162px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		@include sm {
			flex-wrap: wrap;
			padding: 0; } }
	&__img {
		width: 50%;
		img {
			display: block;
			max-width: 100%;
			box-shadow: 0 60px 80px -21px rgba(0, 0, 0, 0.38);
			@include ms {
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				max-width: none;
				width: 400px; } }
		@include sm {
			width: 100%; }
		@include ms {
			width: 100%;
			position: relative;
			height: 255px;
			overflow: hidden; } }
	&__text {
		width: 50%;
		padding-left: 60px;
		margin-top: -1em;
		@include md {
			padding-left: 30px;
			margin-top: 0; }
		@include sm {
			padding-left: 0;
			width: 100%; } }
	&__title {
		padding-bottom: 22px;
		// margin-top: -0.4em
		@include sm {
			margin-top: 50px;
			padding-bottom: 15px; }
		&.title {
			@include md {
				font-size: 30px; } } }
	&__dots {
		position: absolute;
		right: 28px;
		bottom: -107px; } }

// DOTS
.dots {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	max-width: 54px;
	width: 100%;
	&__item {
		width: 8px;
		height: 8px;
		margin: 4px 5px;
		background-color: $black; }
	@include sm {
		display: none; } }

// SEMINAR
.seminar-block {
	&__col {
		@include sm {
			&:not(:first-child) {
				margin-top: 30px; } } } }

// COURSE
.course-block {
	&__col {
		@include md {
			margin-bottom: 16px; } }
	&__btn {
		margin-top: 18px; } }

// EVENT
.event-block {
	@include sm {
		overflow: visible; }
	&__head {
		display: flex;
		align-items: center;
		flex-wrap: wrap; }
	&__title {
		max-width: 33%;
		@include md {
			max-width: none;
			width: 100%; }
		// +ms
 }		// 	padding: 0 16px
	&__list {
		padding-top: 60px;
		@include sm {
			padding-top: 30px; } }
	&__col {
		@include sm {
			&:not(:first-child) {
				margin-top: 30px; } } }
	&__tabs {
		margin-top: 30px;
		margin-left: auto;
		margin-right: 32px;
		@include lg {
			margin-right: 0; }
		@include md {
			margin-left: 0; }
		@include sm {
			display: none; } }
	&__btn {
		margin-top: 30px;
		@include lg {
			display: none; } }
	&__btn-mob {
		@include lg {
			display: block;
			text-align: center;
			margin-top: 37px; } } }


// testimonial
.testimonial-block {
	&__row {
		@include sm {
			white-space: nowrap;
			overflow: hidden;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			&.slick-initialized {
				white-space: normal;
				overflow: visible;
				opacity: 1;
				visibility: visible; } } } }

// CONTACTS
.contacts-block {
	padding-bottom: 128px;
	@include md {
		padding-bottom: 0; }
	@include sm {
		overflow: visible; }
	&__col {
		@include md {
			padding: 0;
			&:not(:last-child) {
				margin-bottom: 40px; } } }
	&__map {
		@include md {
			margin: 0 -16px; } } }
