.basket-page {
	&__title {
		margin-bottom: 60px; }
	&__basket {
		@include sm {
			margin: 0 -16px; } }
	&__descr {
		margin-bottom: 42px;
		padding-top: 70px;
		@include sm {
			padding-top: 32px; }
		p {
			&:not(:last-child) {
				margin-bottom: 5px;
				@include lg {
					margin-bottom: 0; }
				@include sm {
					line-height: 2;
					margin-bottom: 20px; } } } } }

.basket {
	display: flex;
	flex-wrap: wrap;
	@include sm {
		flex-direction: column; }
	&__info {
		@include sm {
			margin-bottom: 10px; } } }

.basket-info,
.basket-total {
	// @extend %cardShadow
	border-radius: $radius;
	background-color: $white;
	box-shadow: $shadow;
	height: 100%; }

%basketBold {
	color: $black;
	@include verdana;
	@include f(15px, 700, 1.667);
	letter-spacing: 0; }

.basket-info {
	display: flex;
	position: relative;
	@include sm {
		flex-direction: column; }
	&__name,
	&__duration {
		border-right: 2px solid #f0f0f3;
		@include sm {
			border-right: none;
			border-bottom: 2px solid #f0f0f3; } }
	&__name {
		@include flex-size(51%);
		padding: 30px;
		@include lg {
			padding: 20px 30px; }
		@include md {
			padding: 15px 10px 15px 20px; }
		@include sm {
			padding: 10px 18px 30px; }
		@include sm {
			@include flex-size(100%); } }
	&__duration {
		@include flex-size(19.5%); }
	&__price {
		@include flex-size(20%); }
	&__duration,
	&__price {
		padding: 30px 10px 30px 40px;
		@include lg {
			padding: 20px 10px 20px 20px; }
		@include md {
			padding: 15px 10px; }
		@include sm {
			@include flex-size(100%);
			padding: 30px 18px; } }
	&__title {
		color: $grayLight;
		font-family: Verdana;
		font-size: 15px;
		letter-spacing: 0.033em;
		line-height: 1.867;
		margin-bottom: 8px;
		@include lg {
			font-size: 12px; }
		@include sm {
			font-size: 15px; } }
	&__text {
		@extend %basketBold;
		@include lg {
			font-size: 12px; }
		@include sm {
			font-size: 15px; } }
	&__delete {
		position: absolute;
		top: 50%;
		right: 30px;
		transform: translateY(-50%);
		@include md {
			right: 15px; }
		@include sm {
			top: auto;
			right: 15px;
			bottom: 36px;
			transform: none; } } }

.basket-total {
	@extend %basketBold;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 30px;
	@include md {
		padding: 15px; }
	@include sm {
		padding: 30px; } }

.checkout-form {
	display: flex;
	flex-direction: column;
	&__inputs {
		+ .checkout-form__inputs {
			margin-top: 20px;
			@include md {
				margin-top: 20px; }
			@include sm {
				margin-top: 20px; } }
		@include sm {
			& > *:not(:last-child) {
					margin-bottom: 20px; } } }
	&__title-wrapper {
		margin-top: 38px;
		margin-bottom: 22px;
		@include md {
			margin-top: 40px;
			margin-bottom: 30px; } }
	&__payment {
		margin-bottom: 69px;
		@include md {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 40px;
			& > *:not(:last-child) {
				margin-bottom: 20px; } } }
	&__agreement {
		margin-right: -96px;
		margin-bottom: 20px;
		@include lg {
			margin-right: 0; }
		@include sm {
			margin-bottom: 0;
			order: 1; } }
	&__submit {
		@include sm {
			margin-bottom: 18px; } }
	.help-block.form-error {
		position: absolute;
		top: 100%;
		left: 0;
		color: $errorRed; }
	.form-group {
		&.has-error {
			margin-bottom: 12px; } } }

.btn-checkbox {
	position: relative;
	width: 100%;
	// max-width: 318px
	padding: 19px 7px;
	text-align: center;
	display: block;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	@include vertical;
	user-select: none;
	@include md {
		padding: 17px 10px; }
	input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: 0;
		clip: rect(0 0 0 0);
		overflow: hidden;
		&:checked {
			~ .btn-checkbox__bg {
				background-color: $blue;
				box-shadow: 0 28px 25px -22px #0053a5; }
			~ .btn-checkbox__text {
				color: $white; } } }
	&__text {
		position: relative;
		z-index: 3;
		display: inline-block;
		vertical-align: middle;
		color: $black;
		@include verdana;
		@include f(18px, 700, 1.778);
		transition: color 0.25s;
		letter-spacing: 0;
		@include lg {
			font-size: 15px; } }
	&__bg {
		@include coverdiv;
		background-color: $white;
		border-radius: $radius;
		transition: box-shadow 0.25s;
 } }		// box-shadow: 0 20px 42px -24px rgba(0,0,0,.3)



