.btn {
	display: inline-block;
	border: none;
	color: $white;
	text-decoration: none;
	background-color: $orange;
	padding: 19px 27px 20px;
	font-size: 15px;
	@include verdana;
	font-weight: 400;
	letter-spacing: 0.066em;
	border-radius: 10px;
	text-align: center;
	vertical-align: top;
	position: relative;
	overflow: hidden;
	outline: none;
	transition: background-color .3s, box-shadow .3s;
	line-height: 1.4;
	cursor: pointer;
	box-shadow: 0 12px 20px -6px $orange;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	// &:before
	// 	content: ''
	// 	position: absolute
	// 	top: 0
	// 	left: 0
	// 	right: 0
	// 	bottom: 0
	// 	transition: background-color .3s
	@include hover {
		background-color: $orangeHover;
		box-shadow: none;
		// background-color: $orange
		// background-image: linear-gradient(to right,rgba(0,0,0,0.15), rgba(0,0,0,0.15))
 }		// &:before
	@include ms {
		width: 100%; }
	&_bold {
		text-transform: uppercase;
		font-weight: 700;
		padding: 1.4em 1.8em;
		@include md {
			font-size: 10px;
			padding: 13px 18px; } }
	&_blue {
		background-color: $white;
		box-shadow: none;
		color: $grayLight;
		transition: background-color .3s, box-shadow .3s, color .3s;
		&.is-active {
			color: $white;
			background-color: $blue;
			box-shadow: 0 19px 25px -12px $blue; }
		@include hover {
			color: $white;
			background-color: $blueCityHover;
			box-shadow: 0 19px 25px -12px $blue; }
		// +md
 }		// 	padding: 15px 20px
	&_white {
		background-color: $white;
		color: $grayLight;
		box-shadow: none;
		font-weight: 700;
		@include hover {
			color: $white; } }
	&_md {
		max-width: 280px;
		box-shadow: none;
		width: 100%;
		@include sm {
			padding: 15px 27px 14px; } }
	&_w100 {
		width: 100%; }
	&_bsnone {
		box-shadow: none; }
	&_big {
		padding: 24px 15px 25px;
		font-weight: 700;
		@include sm {
			padding: 19px 20px 20px; } }
	&_small {
		padding: 1em 1.8em;
		letter-spacing: 0.062em;
		@include lg {
			font-size: 12px; }
		@include sm {
			font-size: 15px; }
		@include hover {
 } } }			// box-shadow: 0 12px 20px -6px $orange


.btn-back {
	background-color: $white;
	box-shadow: 0 7px 36px -18px rgba(0, 0, 0, 0.5);
	color: $text;
	padding: 15px 20px 15px 50px;
	display: inline-block;
	border: none;
	text-decoration: none;
	font-size: 15px;
	@include verdana;
	font-weight: 400;
	letter-spacing: 0.003em;
	border-radius: 10px;
	text-align: center;
	vertical-align: top;
	position: relative;
	outline: none;
	transition: background-color .3s, box-shadow .3s, color .3s;
	line-height: 1.4;
	cursor: pointer;
	@include hover {
		color: $white;
		background-color: $orange; }
	.icon {
		position: absolute;
		top: 50%;
		left: 15px;
		transform: translateY(-50%); } }
.plus {
	display: block;
	@include circle(80);
	background-color: $blue;
	box-shadow: 0 21px 27px -12px $blue;
	position: relative;
	font-size: 40px;
	color: $white;
	transition: box-shadow .3s, background-color .3s;
	@include sm {
		@include circle(60);
		font-size: 30px; }
	.icon {
		position: absolute;
		@include vcenter; } }

.play {
	display: block;
	@include circle(60);
	background-color: $orange;
	position: relative;
	font-size: 30px;
	color: $white;
	transition: transform .2s;
	@include md {
		.my-course__video & {
			@include circle(40);
			font-size: 20px; } }
	@include sm {
		@include circle(40);
		font-size: 20px; }
	.icon {
		position: absolute;
		@include vcenter;
		margin-left: 2px; } }

.delete-btn {
	display: inline-block;
	@include circle(36);
	background-color: $red;
	@include vertical;
	transition: opacity 0.3s;
	@include hover {
		opacity: 0.8; }
	.icon {
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
		color: $white; } }
