.title {
	@include verdana;
	@include f(44px, 700, 1.23);
	color: $black;
	letter-spacing: normal;
	@include md {
		font-size: 25px;
		line-height: 1.33; }
	&_xl {
		font-size: 36px;
		line-height: 1.5;
		@include sm {
			font-size: 25px;
			line-height: 1.24; } }
	&_lg {
		font-size: 30px;
		line-height: 1.34;
		@include md {
			font-size: 20px; } }
	&_md {
		font-size: 21px;
		line-height: 1.48;
		@include md {
			font-size: 19px; } }
	&_sm {
		font-size: 18px;
		line-height: 1.72;
		@include md {
			font-size: 15px; } }
	&_white {
		color: $white; }
	&_line {
		span {
			display: inline-block;
			position: relative;
			padding-right: 2.25em;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				// margin-top: -3px
				right: 0;
				width: 1.86em; // 82px
				height: 6px;
				background-color: currentColor; } } } }

.title-m {
	@include verdana;
	@include f(30px, 700, 1.8);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 25px;
		line-height: 1.24; } }
.title-lg {
	@include verdana;
	@include f(44px, 700, 1.22);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 30px;
		line-height: 1.33; } }


.article,
article {
	max-width: 900px;
	p {
		line-height: 1.95;
		letter-spacing: -.003em;
		@include sm {
			line-height: 1.8; }
		&:not(:first-child) {
			margin-top: 18px; }
		&:not(:last-child) {
			margin-bottom: 18px; } }
	ul {
		&:not(:first-child) {
			margin-top: 10px; }
		li {
			padding: 9px 0 9px 30px;
			position: relative;
			&:before {
				content: '';
				position: absolute;
				background-color: $text;
				left: 0;
				top: 50%;
				height: 1px;
				width: 15px; } } }
	&__title,
	h1 {
		&:not(:last-child) {
			margin-bottom: 30px;
			@include sm {
				margin-bottom: 18px; } } }
	h1, h4, h5 {
		@extend .title;
		&:not(:first-child) {
			margin-top: 18px; } }
	h4 {
		@extend .title_sm;
		&:not(:first-child) {
			margin-top: 8px; } }
	h5 {
		font-size: 18px;
		@include md {
			font-size: 18px; }
		&:not(:first-child) {
			margin-top: 30px; }
		&:not(:last-child) {
			margin-bottom: 12px;
			@include sm {
				margin-bottom: 6px; } }
		& + p:not(:first-child) {
			margin-top: 0; }
		span {
			border-bottom: 3px solid #FF833E; } } }

.subtitle {
	@include verdana;
	@include f(18px, 700, 1.23);
	color: $text;
	letter-spacing: normal; }

.user-agreement {
	color: rgba($black, 0.4);
	@include verdana;
	@include f(13px, 400, 1.384);
	letter-spacing: 0.028em;
	@include sm {
		font-size: 11px; } }
