.online-courses {
	&__main-list {
		margin-top: 60px;
		@include sm {
			margin-top: 40px;
			.personal-office & {
				margin-top: 0; } } }
	&__row {
		margin: 0 -22px;
		@include lg {
			margin: 0 -15px; } }
	&__main-col {
		padding: 0 22px;
		@include lg {
			padding: 0 15px; }
		.personal-office & {
			@include xs {
				margin-top: 40px; } } }
	&__wrap {
		margin-top: 120px;
		padding-top: 50px;
		padding-bottom: 120px;
		background-color: $white;
		@include sm {
			margin-top: 80px;
			padding-top: 26px;
			padding-bottom: 80px; } }
	&__list {
		&:not(:first-child) {
			margin-top: 30px; } }
	&__col {
		margin-top: 30px;
		@include md {
			margin-top: 16px; } }
	.small-dots {
		padding-top: 30px; } }

