.my-course {
	&__main {
		margin-top: 50px;
		display: flex;
		@include sm {
			margin-top: 30px;
			flex-wrap: wrap;
			justify-content: center; } }
	&__left {
		max-width: 423px;
		@include ms {
			max-width: none; }
		.course {
			height: auto; } }
	&__right {
		max-width: 700px;
		padding-left: 50px;
		@include md {
			padding-left: 30px; }
		@include sm {
			padding-left: 0;
			margin-top: 40px; } }
	&__video {
		margin-top: 30px;
		@include sm {
			margin: 30px -16px 0; } } }
